.topMenu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 20px;
  padding-bottom: 35px;
  &.dark {
    color: #fff;
    .search {
      background: #fff;
    }
  }
  ul {
    display: flex;
    li {
      margin: 0 15px 0 0;
      font-size: 16px;
    }
  }
  .search {
    display: flex;
    align-items: center;
    box-shadow: 4px 4px 15px 0px #00000026;
    border: 1px solid #E4E4E4;
    border-radius: 100px;
    input {
      font-family: 'Inter', sans-serif;
      height: 35px;
      width: 265px;
      padding: 0px 15px;
      background: none;
    }
    .searchBtn {
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 35px;
      width: 40px;
      img {
        user-select: none;
      }
    }
  }
}